// 
// responsive-table.scss
//

.table-rep-plugin {
  .dropdown-menu li.checkbox-row {
    padding: 7px 15px;
    color: $gray-600;
    &:hover,
    &:active,
    &:focus{
      background: lighten($light,4%);
      color: $gray-600;
    }
  }

  .table-responsive {
    border: none;
  }
  tbody {
    th {
      font-size: 14px;
      font-weight: normal;
    }
  }
  .checkbox-row {
    padding-left: 40px;

    label {
      display: inline-block;
      padding-left: 5px;
      position: relative;
      margin-bottom: 0;
      &::before {
        -o-transition: 0.3s ease-in-out;
        -webkit-transition: 0.3s ease-in-out;
        background-color: lighten($light, 4%);
        border-radius: 3px;
        border: 1px solid $text-muted;
        content: "";
        display: inline-block;
        height: 17px;
        left: 0;
        margin-left: -20px;
        position: absolute;
        transition: 0.3s ease-in-out;
        width: 17px;
        outline: none;
      }
      &::after {
        color: $gray-300;
        display: inline-block;
        font-size: 11px;
        height: 16px;
        left: 0;
        margin-left: -20px;
        padding-left: 3px;
        padding-top: 1px;
        position: absolute;
        top: -1px;
        width: 16px;
      }
    }
    input[type="checkbox"] {
      cursor: pointer;
      opacity: 0;
      z-index: 1;
      outline: none;

      &:disabled + label {
        opacity: 0.65;
      }
    }
    input[type="checkbox"]:focus + label {
      &::before {
        outline-offset: -2px;
        outline: none;
      }
    }
    input[type="checkbox"]:checked + label {
      &::after {
           content: "\f00c";
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
      }
    }
    input[type="checkbox"]:disabled + label {
      &::before {
        background-color: $light;
        cursor: not-allowed;
      }
    }
    input[type="checkbox"]:checked + label {
      &::before {
        background-color: lighten($light, 4%);
        border-color: lighten($light, 8%);
      }
      &::after {
        color: $body-color;
      }
    }
  }
  table.focus-on tbody tr.focused th, table.focus-on tbody tr.focused td,
  .sticky-table-header{
    background: $primary;
    background: $bg-gradient;
    color: $white;
    border-color: $primary;
    table{
        color: $white;
    }
  }
  .fixed-solution {
      .sticky-table-header {
          top: 130px !important;
      }
  }
  .btn-default {
    background-color: lighten($light, 4%);
    border: 1px solid lighten($light, 8%);
    color: $dark;
  }
  .btn-primary {
      background: $bg-gradient;
      border-color: $primary;
      color: $white;
  }
  .btn-group.pull-right {
    float: right;
    .dropdown-menu {
      left: auto;
      right: 0;
    }
  }
}

.table-rep-plugin .btn-toolbar {
  display: block;
}
